import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import LoginView from '../views/LoginView.vue'
import MainView from '../views/MainView'
import RegistrationView from '../views/RegistrationView'
import MaininView from '../views/MaininView.vue'
import traningsView from '../views/tranings.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/login',
    name: 'LoginView',
    component: LoginView
  },
  {
    path: '/main',
    name: 'MainView',
    component: MainView
  },
  {
    path: '/mainin',
    name: 'MaininView',
    component: MaininView
  },
  {
    path: '/registration',
    name: 'RegistrationView',
    component: RegistrationView
  },
  {
    path: '/tranings',
    name: 'traningsView',
    component: traningsView
  }


]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
